@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Staatliches&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:'Outfit', sans-serif;;
}

body {
  background: black;
}

h1, h4, p, a {
  color: #ddd;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.btn {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(248, 217, 15);
  color:#222;
  border: 1px solid #ddd;
  font-weight: 600;
  cursor: pointer;
}

.btn-light {
  background: transparent;
  color: #ddd;
}

.btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #ddd;
  transition: 0.3s;
}